import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import Sites from "./views/Sites.vue";
import Site from "./views/Site.vue";
import Buttons from "./views/Buttons.vue";
import TheButton from "./views/Button.vue";
import Clicks from "./views/Clicks.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: "/clicks",
      name: "Clicks",
      component: Clicks
    },
    {
      path: "/buttons",
      name: "Buttons",
      component: Buttons
    },
    {
      path: "/buttons/:buttonSlug",
      name: "button",
      component: TheButton
    },
    {
      path: "/sites",
      name: "sites",
      component: Sites
    },
    {
      path: "/sites/:siteEncodedURL",
      name: "site",
      component: Site
    },
  ]
});
