<template>
  <div class="view view--clicks">
    <h2>Raw Click Log</h2>
    <div class="view__inner" v-if="loading == false">
      <p class="row-count">Showing rows 0 - {{ clicks.length }}</p>
      <section class="clicks">
        <ClickInfo 
        v-bind:clicks="clicks"
        v-bind:sortBy="getSort"
        ></ClickInfo>
        <div class="clicks__status">
          <p class="row-count">Showing Rows 0 - {{ clicks.length }}</p>
          <div class="clicks__add-rows" v-if="allClicks === false">
            <button class="btn btn--add-clicks" v-on:click="fetchMoreClicks(100, $event)">100 More</button>
            <button class="btn btn--add-clicks" v-on:click="fetchMoreClicks('all', $event)">All</button>
          </div>
        </div>
      </section>
    </div>
    <Loader v-else-if="loading"/>
  </div>
</template>

<script>
// @ is an alias to /src
import ClickInfo from "@/components/ClickInfo.vue";
import Loader from "@/components/Loader.vue";
import { mapGetters } from "vuex";

export default {
  name: "Clicks",
  data: function() {
    return {
      page: 0,
      count: 100,
      allClicks: false,
      loading: true
    };
  },
  components: {
    ClickInfo,
    Loader
  },
  created() {
    // fetch the data when the view is created and the data is
    // already being observed
    this.fetchData();
  },
  watch: {
    // call again the method if the route changes
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      this.loading = true
      return this.$store.dispatch("fetchClicks", this.count).then(() => this.loading = false);
    },
    fetchMoreClicks(count, event) {
      this.loading = true
      let currentClickLength = this.clicks.length
      
      // keep fetching clicks until they're all done 
      this.$store.dispatch("fetchClicks", this.count).then(() => {
        // if the previous click length matches the new one, we know we've gotten all of them
        if(currentClickLength !== this.clicks.length) {
          // do we need to get more?
          if(count === 'all') {

            // goin' recursive!
            this.fetchMoreClicks('all', event)
          } else {
            this.loading = false
          }
        } else {
          // we have all of them. 
          console.log('all clicks found')
          this.allClicks = true
          this.loading = false
        }
      })      
      // just increase the count by 100 so we show all of them so we don't paginate for now
      // this.count += 100
    },
    clicksSlice() {
      return this.clicks.slice(this.page, ((this.page + 1) * this.count))
    }
  },
  computed: {
    getSort: function() {
      return {
        field: "date",
        order: "desc"
      };
    },
    ...mapGetters(["error", "clicks"])
  }
};
</script>

