<template>
  <ButtonInfo
    v-if="loading == false"
    v-bind:key="getButton.id"
    v-bind:button="getButton"
    v-bind:sortBy="getSort"
  ></ButtonInfo>
  <Loader v-else-if="loading"/>
</template>

<script>
// @ is an alias to /src

import ButtonInfo from "@/components/ButtonInfo.vue";
import Loader from "@/components/Loader.vue";
import { mapGetters } from "vuex";

export default {
  name: "theButton",
  data: function() {
    return {
      loading: true
    };
  },
  components: {
    ButtonInfo,
    Loader
  },
  created() {
    // fetch the data when the view is created and the data is
    // already being observed
    this.fetchData();
  },
  watch: {
    // call again the method if the route changes
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      this.loading = true
      return this.$store.dispatch("fetchButtonClicks", this.$route.params.buttonSlug).then(() => this.loading = false);
    }
  },
  computed: {
    getButton: function() {
      return this.button(this.$route.params.buttonSlug);
    },
    getSort: function() {
      return {
        field: "date",
        order: "desc"
      };
    },
    ...mapGetters(["error", "buttons", "button"])
  }
};
</script>
