<template>
  <div class="view view--buttons" v-if="loading == false">
    <section class="buttons">
      <ButtonData 
        v-for="button in buttons"
        v-bind:key="button.slug"
        v-bind:button="button"></ButtonData>
    </section>
  </div>
  <Loader v-else-if="loading"/>
</template>

<script>
// @ is an alias to /src
import ButtonData from "@/components/ButtonData.vue";
import Loader from "@/components/Loader.vue";
import { mapGetters } from "vuex";

export default {
  name: "Buttons",
  data: function() {
    return {
      loading: true
    };
  },
  components: {
    ButtonData,
    Loader
  },
  created() {
    // fetch the data when the view is created and the data is
    // already being observed
    this.fetchData();
  },
  watch: {
    // call again the method if the route changes
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      this.loading = true
      return this.$store.dispatch("fetchButtons").then(() => this.loading = false)
    }
  },
  computed: {
    ...mapGetters(["error", "buttons"])
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../scss/_vars.scss";
.buttons {
  @include cardsGrid;
}
</style>
