<template>
  <div class="view view--sites" v-if="loading == false">
    <div class="filter">
      <label>Search Sites<br/>
        <input v-model="search" class="filter__input" type="text" />
      </label>
    </div>
    <section class="sites" v-if="filteredSites.length > 0">
      <SitePreview 
      	v-for="site in filteredSites"
      	v-bind:key="site.id"
        v-bind:site="site"></SitePreview>
  	</section>
    <section class="no-sites" v-else>
      No sites found for search "{{ search }}"
    </section>
  </div>
  <Loader v-else-if="loading"/>
</template>

<script>
// @ is an alias to /src
import SitePreview from "@/components/SitePreview.vue";
import Loader from "@/components/Loader.vue";
import { mapGetters } from "vuex";

export default {
  name: "sites",
  data: function() {
    return {
      search: "",
      loading: true
    };
  },
  components: {
    SitePreview,
    Loader
  },
  created() {
    // fetch the data when the view is created and the data is
    // already being observed
    this.fetchData();
  },
  watch: {
    // call again the method if the route changes
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      this.loading = true
      return this.$store.dispatch("fetchSites").then(() => this.loading = false);
    }
  },
  computed: {
    filteredSites: function() {
      return this.filterSites(this.search);
    },
    ...mapGetters(["error", "sites", "server", "filterSites"])
  }
};
</script>

