<template>
  <div class="button-info">
      <h2 class="button__name">{{ button.name }}</h2>
      <table>
        <thead>
          <th>URL
            <span class="order-wrap"><button class="btn--icon up" v-on:click="sortButtonsBy('button_url', 'asc')"><svg class="icon-up"><use xlink:href="#icon-up"></use></svg></button><button class="btn--icon down" v-on:click="sortButtonsBy('button_url', 'desc')"><svg class="icon-down"><use xlink:href="#icon-down"></use></svg></button></span>
          </th>
          <th>Post Type 
            <span class="order-wrap"><button class="btn--icon up" v-on:click="sortButtonsBy('post_type', 'asc')"><svg class="icon-up"><use xlink:href="#icon-up"></use></svg></button><button class="btn--icon down" v-on:click="sortButtonsBy('post_type', 'desc')"><svg class="icon-down"><use xlink:href="#icon-down"></use></svg></button></span>
          </th>
          <th>Date 
            <span class="order-wrap"><button class="btn--icon up" v-on:click="sortButtonsBy('date', 'asc')"><svg class="icon-up"><use xlink:href="#icon-up"></use></svg></button><button class="btn--icon down" v-on:click="sortButtonsBy('date', 'desc')"><svg class="icon-down"><use xlink:href="#icon-down"></use></svg></button></span></th>
        </thead>
        <tbody>
          <tr 
            v-for="click in button.clicks">
            <td class="td__url"><a :href="click.button_url">{{ click.button_url }}</a></td>
            <td>{{ click.post_type }}</td>
            <td>{{ click.date }}</td>
          </tr>
        </tbody>
      </table>
  </div>
</template>

<script>
export default {
  name: "ButtonInfo",
  props: {
    button: Object,
    sortBy: Object
  },
  created() {
    // if we have clicks, sort them
    if (this.button.clicks) {
      this.sortButtons(this.sortBy.field, this.sortBy.order);
    }
  },
  methods: {
    sortButtonsBy: function(field, order) {
      // don't run the sort if it's what we're already sorted by
      if (this.sortBy.field !== field || this.sortBy.order !== order) {
        this.sortButtons(field, order);
      }
    },
    sortButtons: function(field, order) {
      this.button.clicks.sort(this.compareValues(field, order));
      // set the new sort value
      this.sortBy.field = field;
      this.sortBy.order = order;
    },
    compareValues: function(key, order = "asc") {
      return function(a, b) {
        if (!a.hasOwnProperty(key) || !b.hasOwnProperty(key)) {
          // property doesn't exist on either object
          return 0;
        }

        const varA = typeof a[key] === "string" ? a[key].toUpperCase() : a[key];
        const varB = typeof b[key] === "string" ? b[key].toUpperCase() : b[key];

        let comparison = 0;
        if (varA > varB) {
          comparison = 1;
        } else if (varA < varB) {
          comparison = -1;
        }
        return order == "desc" ? comparison * -1 : comparison;
      };
    }
  }
};
</script>

