import Vue from "vue";
import Vuex from "vuex";
import Axios from "axios";

Vue.use(Vuex);

const server = Axios.create({
  baseURL:
    (/(?:localhost:8080|.+\.test)/.test(
      window.location.host
    ) === false
      ? "https://data.mediaengagement.org"
      : "https://cme-data.test") + "/engaging-buttons/api/v1",
  timeout: 5000
});

export default new Vuex.Store({
  state: {
    server: server,
    loading: true,
    fetching: false,
    error: false,
    sites: undefined,
    buttons: undefined,
    clicks: []
  },
  mutations: {
    setError: function(state, val) {
      state.error = val
    },
    setLoading: function(state, val) {
      state.loading = val
    },
    setFetching: function(state, val) {
      state.fetching = val
    },
    setClicks: function(state, clicks) {
      console.log("clicks set")
      state.clicks = clicks
    },
    setButtons: function(state, buttons) {
      console.log("buttons set")
      state.buttons = buttons
    },
    setButtonClicks: function(state, obj) {
      state.buttons[obj.buttonIndex].clicks = obj.button.clicks
    },
    setSites: function(state, sites) {
      console.log("sites set")
      state.sites = sites
    },
    setSitePosts: function(state, obj) {
      state.sites[obj.siteIndex].posts = obj.site.posts
    }
  },
  getters: {
    loading: state => state.loading,
    fetching: state => state.fetching,
    error: state => state.error,
    server: state => state.server,
    clicks: state => state.clicks,
    buttons: state => state.buttons,
    button: (state, getters) => slug => {
      return getters.buttons.find(button => button.slug === slug);
    },
    buttonIndexBy: (state, getters) => (name, val) => {
      return getters.indexBy(getters.buttons, name, val);
    },
    sites: state => state.sites,
    filterSites: state => search => {
      // if it's empty return all sites
      if (search == "") {
        return state.sites;
      }

      return state.sites.filter(function(site) {
        return site.url.includes(search);
      });
    },
    site: (state, getters) => encodedURL => {
      return getters.sites.find(site => site.encodedURL === encodedURL);
    },
    siteIndexBy: (state, getters) => (name, val) => {
      return getters.indexBy(getters.sites, name, val);
    },
    /**
     * Powers most all of the retrieval of data from the tree
     * Searches an array for a key that equals a certain value
     *
     * @param objArray (ARRAY of OBJECTS)
     * @param name (STRING) of the key you're wanting to find the matching value of
     * @param value (MIXED) the value you want to find a match for
     * @return INT of the index that matches or UNDEFINED if not found
     */
    indexBy: () => (objArray, name, value) => {
      for (let i = 0; i < objArray.length; i++) {
        if (objArray[i][name] == value) {
          return i;
        }
      }
      return undefined;
    }
  },
  actions: {
    error(context, val) {
      context.commit("setError", val);
    },
    loading(context, val) {
      context.commit("setLoading", val);
    },
    fetching(context, val) {
      context.commit("setFetching", val);
    },
    sitePosts(context, val) {
      context.commit("setSitePosts", val);
    },
    fetchClicks(context, count) {
      console.log("fetching clicks");
      context.dispatch("fetching", true);
      // decide what our params should be
      let page = 0
      if(count === undefined) {
        count = 100
      }
      
      let clicks = context.getters.clicks

      if(clicks.length) {
        page = Math.ceil(clicks.length/count)
      }
      return context.getters
        .server("/clicks?count="+count+"&page="+page)
        .then(response => {
          console.log("fetched clicks");
          let returnedClicks = response.data;
          console.log(returnedClicks)
          for (var i = 0; i < returnedClicks.length; i++) {
            // add in an ID
            // using returnedClicks.length + 1 because we might fetch more
            returnedClicks[i].key = (clicks.length + i) + 1
          }
          // append the clicks ot the current ones
          clicks = clicks.concat(returnedClicks)
          // set the clicks
          context.commit("setClicks", clicks);
        })
        .catch(error => {
          console.error(error);
          context.dispatch("error", true);
        })
        .finally(() => {
          context.dispatch("fetching", false);
        });
    },
    fetchButtons(context) {
      console.log("fetching buttons");
      context.dispatch("fetching", true);

      return context.getters
        .server("/buttons")
        .then(response => {
          console.log("fetched buttons");
          let buttons = response.data;
          for (var i = 0; i < buttons.length; i++) {
            // add in an ID
            buttons[i].key = i
            // add in empty clicks data
            buttons[i].clicks = undefined
          }
          context.commit("setButtons", buttons);
        })
        .catch(error => {
          console.error(error);
          context.dispatch("error", true);
        })
        .finally(() => {
          context.dispatch("fetching", false);
        });
    },
    fetchButtonClicks(context, slug) {
      // see if sites have been sept yet
      if (context.getters.sites === undefined) {
        return context.dispatch("fetchButtons").then(() => {
          context.dispatch("asyncFetchButtonClicks", slug);
        });
      } else {
        return context.dispatch("asyncFetchButtonClicks", slug);
      }
    },
    asyncFetchButtonClicks(context, slug) {
      console.log("fetching button clicks");
      context.dispatch("fetching", true);

      return context.getters.server
        .get("/buttons/" + slug)
        .then(response => {
          console.log("fetched button clicks");
          // add this into the button data in the buttons store
          // let button = response.data;
          console.log("clicks", response.data);
          context.commit("setButtonClicks", {
            button: response.data,
            buttonIndex: context.getters.buttonIndexBy("slug", slug)
          });
        })
        .catch(error => {
          console.error(error);
          context.dispatch("error", true);
        })
        .finally(() => {
          context.dispatch("fetching", false);
        });
    },
    fetchSites(context) {
      console.log("fetching sites");
      context.dispatch("fetching", true);

      return context.getters
        .server("/sites")
        .then(response => {
          console.log("fetched sites");
          let sites = response.data;
          for (var i = 0; i < sites.length; i++) {
            // add in an ID
            sites[i].key = i;
            // add in the encodedURL
            sites[i].encodedURL = encodeURIComponent(sites[i].url);
            // add in a spot for posts
            sites[i].posts = undefined;
          }
          context.commit("setSites", sites);
        })
        .catch(error => {
          console.error(error);
          context.dispatch("error", true);
        })
        .finally(() => {
          context.dispatch("fetching", false);
        });
    },
    fetchSitePosts(context, encodedURL) {
      // see if sites have been set yet
      if (context.getters.sites === undefined) {
        return context.dispatch("fetchSites").then(() => {
          context.dispatch("asyncFetchSitePosts", encodedURL);
        });
      } else {
        return context.dispatch("asyncFetchSitePosts", encodedURL);
      }
    },
    asyncFetchSitePosts(context, encodedURL) {
      console.log("fetching site posts");
      context.dispatch("fetching", true);

      return context.getters.server
        .get("/sites/" + encodedURL)
        .then(response => {
          console.log("fetched site posts");
          // add this into the site data in the sites store
          // let site = response.data;
          console.log("posts", response.data);
          context.commit("setSitePosts", {
            site: response.data,
            siteIndex: context.getters.siteIndexBy("encodedURL", encodedURL)
          });
        })
        .catch(error => {
          console.error(error);
          context.dispatch("error", true);
        })
        .finally(() => {
          context.dispatch("fetching", false);
        });
    }
  }
});
